////
/// Core Tool : Selector Breakpoint
/// @group core
////

/// Set media query styles
///
/// @param {String} $media [md] - Layout size `['xs', 'sm', 'md', 'lg', 'xl']`
///
/// @example scss -
///   .foo {
///     @include respond-from(md) {
///     }
///   }
@mixin respond-from($media) {
  $limits: map_get($breakpoints, $media);

  @if $limits != null {
    @media (min-width: nth($limits, 1)) {
      @content;
    }
  }
  @else {
    @content;
  }
}

/// Set media query styles
///
/// @param {String} $media [md] - Layout size `['xs', 'sm', 'md', 'lg', 'xl']`
///
/// @example scss -
///   .foo {
///     @include respond-to(md) {
///     }
///   }
@mixin respond-to($media) {

  @debug 'afin de rester mobile first, nutiliser la mixin respond-to uniquement sil ny a pas dautre alternative, lui prfrer systmatiquement la mixin respond-from';

  $limits: map_get($breakpoints, $media);

  @if $limits != null and length($limits) == 2 {
    @media (max-width: nth($limits, 2)) {
      @content;
    }
  }
  @else {
    @content;
  }
}
